import React from 'react'
import {Link} from 'react-router'
import $ from 'jquery'

var Login = React.createClass({
  getInitialState: function() {
    return (
      {
        username: null,
        password: null
      }
    )
  },
  onChangeHandler: function(field, val) {
    var newData = {}
    newData[field] = val
    this.setState(newData)
  },
  onSubmitHandler: function() {
    var self = this;
    $.ajax({
      url: '/logIn',
      method: 'POST',
      data: this.state
    }).done(function(data) {
      console.log(data);
      if (data.message) {
        window.location = '/#/'
        console.log('message: ' + data.message);
      } else {
        self.props.setCurrentUser(data);
        window.location = '/#/';
        console.log('signed in');
      }
    })
  },
  render: function() {
    return (
      <div className='contentContainer'>
        <h3>Log In</h3>
        <form>
          <input type='text' placeholder='username' onChange={(event) => this.onChangeHandler('username', event.target.value)} />
          <input type='text' placeholder='password' onChange={(event) => this.onChangeHandler('password', event.target.value)} />
        </form>
        <button className="btn btn-primary" onClick={(event) => this.onSubmitHandler()}>Log In</button>
      </div>
    )
  }
})

export default Login



